/*-----------------
	44. Timeline
-----------------------*/

.timeline {
	position: relative;
	padding: 20px 0 20px;
	list-style: none;
	max-width: 1200px;
	margin: 0 auto;
}
.timeline:before {
	content: " ";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 50%;
	width: 3px;
	margin-left: -1.5px;
	background-color: #e9ecef;
}
.timeline>li {
	position: relative;
	margin-bottom: 20px;
}
.timeline>li:before,
.timeline>li:after {
	content: " ";
	display: table;
}
.timeline > li:after {
	clear: both; 
}
.timeline>li:before,
.timeline>li:after {
	content: " ";
	display: table;
}
.timeline>li:after {
	clear: both;
}
.timeline>li>.timeline-panel {
	float: left;
	position: relative;
	width: 46%;
	padding: 20px;
	border: 1px solid #e9ecef;
	border-radius: 0.25rem;
	-webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
	box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}
.timeline>li>.timeline-panel:before {
	content: " ";
	display: inline-block;
	position: absolute;
	top: 26px;
	right: -8px;
	border-top: 8px solid transparent;
	border-right: 0 solid #e9ecef;
	border-bottom: 8px solid transparent;
	border-left: 8px solid #e9ecef;
}
.timeline>li>.timeline-panel:after {
	content: " ";
	display: inline-block;
	position: absolute;
	top: 27px;
	right: -7px;
	border-top: 7px solid transparent;
	border-right: 0 solid #fff;
	border-bottom: 7px solid transparent;
	border-left: 7px solid #fff;
}
.timeline>li>.timeline-badge {
	z-index: 1;
	position: absolute;
	top: 16px;
	left: 50%;
	width: 50px;
	height: 50px;
	margin-left: -25px;
	border-radius: 50% 50% 50% 50%;
	text-align: center;
	font-size: 1.4em;
	line-height: 50px;
	color: #fff;
	overflow: hidden;
}
.timeline>li.timeline-inverted>.timeline-panel {
	float: right;
}
.timeline>li.timeline-inverted>.timeline-panel:before {
	right: auto;
	left: -8px;
	border-right-width: 8px;
